import React from 'react'
import Layout from '../components/Layout'
import { makeStyles } from '@material-ui/core'
import clsx from 'clsx'
import FeatureImage from '../components/Home/FeatureImage'
import Features from '../components/Home/Features'
import RateABiz from '../components/Home/RateABiz'
import fetch from 'isomorphic-unfetch'
import rating_local from '../data/rating_local'

const API_URL =
  'https://s3.amazonaws.com/cdn.rateabiz.com/reviews/ce34016f3fd1daf75a0daca4eb322873/reviews.json'

const useStyles = makeStyles(theme => ({
  root: {},
  full: {
    gridColumn: 'full-start / full-end'
  },
  center: {
    gridColumn: 'center-start / center-end'
  },
  featureImage: {
    height: '60vh'
  },
  features: {
    height: '30vw'
  },
  rateABiz: {
    height: 500
  },
  about: {
    background: theme.palette.primary.light,
    height: 800
  },
  cta: {
    background: theme.palette.secondary.light,
    height: 200
  },
  maps: {
    background: theme.palette.primary.light,
    height: 400
  }
}))

export default function Index({ reviews }) {
  const classes = useStyles()
  console.log('reviews', reviews)
  return (
    <Layout>
      <div className={clsx(classes.featureImage, classes.full)}>
        <FeatureImage />
      </div>
      <div className={clsx(classes.features, classes.center)}>
        <Features />
      </div>
      <div className={clsx(classes.rateABiz, classes.full)}>
        <RateABiz reviews={reviews} />
      </div>
      <div className={clsx(classes.about, classes.center)} />
      <div className={clsx(classes.cta, classes.full)} />
      <div className={clsx(classes.maps, classes.center)} />
    </Layout>
  )
}

Index.getInitialProps = async function() {
  // const res = await fetch(API_URL)
  // const { reviews, stats } = await res.json()
  console.log('reviews', reviews)
  return reviews
    .filter(
      ({
        authorLastName,
        createdDate,
        authorFirstName,
        rating,
        id,
        text,
        title
      }) => {
        console.log('authorLastName', authorLastName)
        return !!(authorFirstName && authorLastName && rating && id)
      }
    )
    .map(review => {
      return {
        name: review.authorFirstName + ' ' + review.authorLastName,
        key: review.id,
        review: {
          title: review.title,
          text: review.text
        }
      }
    })
}
